import React from 'react';
import {compose} from 'redux';
import {connect} from 'react-redux';
import {withRouter, Link} from 'react-router-dom';
import {Table, Pagination, Card} from 'antd';
import ManageSearchComponent from './ManageSearchComponent';
import './style.less';
import {getData as studentfindPage} from 'actions/mentalHealth/studentfindPage';
import Alert from 'app/MentalHealthcomponents/CustomComponent/Alert/index';

class MentalhealthManagement extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showTableData: props.studentfindPage['useData'],
            pageSize: 10,
            page: 1
        };
        this.params = {
            pageSize: 10,
            page: 1
        };
    }
    
    componentWillReceiveProps(nextProps) {
        if (nextProps.studentfindPage !== this.props.studentfindPage) {
            if (nextProps.studentfindPage['success']) {
                this.setState({
                    showTableData: nextProps.studentfindPage['useData']
                });
            } else {
                Alert.errorAlert('提示', nextProps.studentfindPage['msg']);
            }
            
        }
    }
    
    componentDidMount() {
        this.props.studentfindPageFun(this.params);
    }
    
    onPageSizeChange(current, size) {
        this.setState({
            page: current,
            pageSize: size
        }, function() {
            var useData = this.params;
            useData['page'] = current;
            useData['pageSize'] = size;
            this.props.studentfindPageFun(useData);
        });
    }
    
    render() {
        const that = this;
        const columns = [{
            title: '序号',dataIndex: 'rowIndex',align:'center',width: "50px",
        }, {
            title: '学生姓名',
            dataIndex: 'name',
            render: function(text,data) {
                return (<Link to={`/mainframe/mentalhealth/management/${data['name']}/${data['code']}`}>{data['name']}</Link>);
            }
        }, {
            title: '学号/教工号',
            dataIndex: 'code',
        }, {
            title: '性别',
            dataIndex: 'sex',
        }, {
            title: '院系',
            dataIndex: 'academy',
        }, {
            title: '专业',
            dataIndex: 'major',
        }, {
            title: '更新时间',
            dataIndex: 'updateTime',
        }, {
            title: '是否添加记录',
            dataIndex: 'isAddRecord',
            render: function(text,data) {
                return (data['isAddRecord'] === '0' ? '否' : '是');
            }
        }, {
            title: '操作',
            render: function(text,data) {
                return (<Link to={`/mainframe/mentalhealth/management/${data['name']}/${data['code']}`}
                              style={{color: '#1890ff'}}>处理</Link>);
            }
        }];
        const data = that.state.showTableData['rows'] || [];
        data.forEach((item,index)=>{
            data[index].key = index;
            data[index].rowIndex = index+1;
        })
        // console.log(this.state.showTableData['total']);
        return (<div className={'MentalhealthManagement'}>
            <ManageSearchComponent useParam={(data) => {this.params = data;}}></ManageSearchComponent>
            <div style={{
                padding: '0px 20px',
                marginTop: 20,
                minHeight: document.body.clientHeight - 195
            }}>
            <Card>
                <Table
                    rowKey="code"
                    columns={columns}
                    dataSource={data}
                    bordered
                    size="middle"
                    pagination={false}
                    style={{backgroundColor:'#fff'}}
                />
                <Pagination style={{marginTop:20,textAlign:'right'}} showQuickJumper defaultCurrent={1}
                    size="small"
                    defaultPageSize={this.state.pageSize}
                    total={that.state.showTableData['total']}
                    onChange={this.onPageSizeChange.bind(this)}
                    showTotal={total => `共 ${total} 条数据`}
                    />
            </Card>
            </div>
        </div>);
    }
}

const mapStateToProps = (state) => {
    return {
        studentfindPage: state.studentfindPage
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        studentfindPageFun: function(data) {
            dispatch(studentfindPage(data));
        }
    };
};

const enchce = compose(connect(mapStateToProps, mapDispatchToProps), withRouter);

export default enchce(MentalhealthManagement);





