import React from 'react';
// import {Link, withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {compose} from 'redux';
// import moment from 'moment';

import AcademiesSearchComponent from 'app/MentalHealthcomponents/CustomComponent/Select/Select';
import MajorComponent from 'app/MentalHealthcomponents/CustomComponent/Select/Select';
import GradeComponent from 'app/MentalHealthcomponents/CustomComponent/Select/Select';
// import SelectType from 'app/MentalHealthcomponents/CustomComponent/Select/Select';
// import WarnSelect from 'app/MentalHealthcomponents/CustomComponent/Select/Select';
// import WarnLevel from 'app/MentalHealthcomponents/CustomComponent/Select/Select';
import Input from 'app/MentalHealthcomponents/CustomComponent/Input/Input';

import {getData as findYxzynList, changeAcademies, changeMajors, initData} from 'actions/findYxzynList';
import Utilies from 'Utilies/Utilies';
import FormattedData from 'Utilies/FormattedData';
import {getData as studentfindPage} from 'actions/mentalHealth/studentfindPage';

class ManageSearchComponent extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            academies: props['YxzynListData']['academies'],
            academiesCode: '0',
            majors: props['YxzynListData']['majors'],
            majorCode: '0',
            grade: props['YxzynListData']['grade'],
            gradeCode: '0',
            WarnSelectData: [{
                id: '0',
                name: '全部'
            }, {
                id: '01',
                name: '已处理'
            }, {
                id: '00',
                name: '待处理'
            }],
            WarnLevelData: [{
                id: '0',
                name: '全部'
            }, {
                id: 1,
                name: '蓝色预警'
            }, {
                id: 2,
                name: '黄色预警'
            }, {
                id: 3,
                name: '橙色预警'
            }, {
                id: 4,
                name: '红色预警'
            }],
            selectKey: '0',
            LevelKey: '0',
            name: '',
            code: ''
            
        };
    }
    
    componentDidMount() {
        this.props.findYxzynListFun();
    }
    
    componentWillUnmount() {
    
    }
    
    componentWillReceiveProps(nextProps) {
        if (nextProps.YxzynListData['academies'] !== this.props.YxzynListData['academies']) {
            this.setState({
                academies: nextProps['YxzynListData']['academies']
            });
        }
        
        if (nextProps.YxzynListData['majors'] !== this.props.YxzynListData['majors']) {
            this.setState({
                majors: nextProps['YxzynListData']['majors']
            });
        }
        
        if (nextProps.YxzynListData['grade'] !== this.props.YxzynListData['grade']) {
            this.setState({
                grade: nextProps['YxzynListData']['grade']
            });
        }
    }
    
    // 选择学院
    changeAcadeHandle=(data)=> {
        if (data === 0) {
            this.props.initDataFun();
            this.setState({
                academiesCode: data,
                majorCode: '0',
                gradeCode: '0'
            });
        } else {
            this.setState({
                academiesCode: data,
                majorCode: '0',
                gradeCode: '0'
            }, function() {
                let allData = Utilies.deepClone(this.props.YxzynListData['allData']);
                let majors = allData['majors'];
                this.props.changeAcademiesFun(FormattedData.nameCodeToSelectAllData(majors[data]));
            });
        }
    }
    
    // 选择专业
    changeMajorHandle=(data)=> {
        if (data === 0) {
            this.setState({
                majorCode: data,
                gradeCode: '0',
                grade: [{
                    id: '0',
                    name: '全部'
                }]
            });
        } else {
            this.setState({
                majorCode: data,
                gradeCode: '0'
            }, function() {
                let allData = Utilies.deepClone(this.props.YxzynListData['allData']);
                let grades = allData['grades'];
                this.props.changeMajorsFun(FormattedData.nameCodeToSelectAllData(grades[data]));
            });
        }
    }
    
    // 选择年级
    changeGradeHandle=(data)=> {
        this.setState({
            gradeCode: data
        });
    }
    
    changeHandle=(val, key)=> {
        this.setState({
            [key]: Utilies.isNull(val) ? '' : val
        });
    }
    
    searchHandle=()=> {
        let {academiesCode, majorCode, gradeCode, name, code} = this.state;
        var useData = {};
        
        useData['name'] = name; // 姓名学号
        useData['code'] = code; // 姓名学号
        useData['page'] = '1'; // 姓名学号
        useData['pageSize'] = '10'; // 姓名学号
        useData['academyCode'] = academiesCode === 0 ? '' : academiesCode;
        useData['majorCode'] = majorCode === 0 ? '' : majorCode;
        useData['gradeCode'] = gradeCode === 0 ? '' : gradeCode;
        
        for (var i in useData) {
            if (useData[i] === '') {
                delete useData[i];
            }
        }
        
        this.props.studentfindPageFun(useData);
        this.props.useParam(useData);
        
    }
    
    render() {
        return (<div className={'top_search_block'} style={{flexWrap: 'wrap'}}>
            <AcademiesSearchComponent
                style={{
                    width: '20%',
                    marginRight: '20px'
                }}
                name={'学院'}
                titleStyle={this.props.showWarn ? {
                    width: '65px',
                    textAlign: 'right'
                } : {}}
                position={'left'}
                allowClear={false}
                onChange={this.changeAcadeHandle}
                value={this.state.academiesCode}
                data={this.state.academies}></AcademiesSearchComponent>
            
            <MajorComponent
                name={'专业'}
                style={{
                    width: '20%',
                    marginRight: '20px'
                }}
                titleStyle={this.props.showWarn ? {
                    width: '65px',
                    textAlign: 'right'
                } : {}}
                position={'left'}
                allowClear={false}
                onChange={this.changeMajorHandle}
                value={this.state.majorCode}
                data={this.state.majors}></MajorComponent>
            
            <GradeComponent
                name={'年级'}
                style={{
                    width: '20%',
                    marginRight: '20px'
                }}
                titleStyle={this.props.showWarn ? {
                    width: '65px',
                    textAlign: 'right'
                } : {}}
                position={'left'}
                allowClear={false}
                onChange={this.changeGradeHandle}
                value={this.state.gradeCode}
                data={this.state.grade}></GradeComponent>
            
            <Input position={'left'} name={'姓名'}
                   style={{
                       width: '20%',
                       marginRight: '20px'
                   }}
            
                   value={this.state.name}
                   datakey={'name'}
                   onChange={this.changeHandle}
                   placeholder={'输入姓名'}/>
            <Input position={'left'} name={'学号'}
                   style={{
                       width: '20%',
                       marginRight: '20px'
                   }}
            
                   value={this.state.code}
                   datakey={'code'}
                   onChange={this.changeHandle}
                   placeholder={'输入学号'}/>
            <button style={{marginLeft: '43px'}} className="btn search_btn" onClick={this.searchHandle}>
            <i className="anticon anticon-search"><svg viewBox="64 64 896 896" data-icon="search" width="1em" height="1em" fill="currentColor" aria-hidden="true"><path d="M909.6 854.5L649.9 594.8C690.2 542.7 712 479 712 412c0-80.2-31.3-155.4-87.9-212.1-56.6-56.7-132-87.9-212.1-87.9s-155.5 31.3-212.1 87.9C143.2 256.5 112 331.8 112 412c0 80.1 31.3 155.5 87.9 212.1C256.5 680.8 331.8 712 412 712c67 0 130.6-21.8 182.7-62l259.7 259.6a8.2 8.2 0 0 0 11.6 0l43.6-43.5a8.2 8.2 0 0 0 0-11.6zM570.4 570.4C528 612.7 471.8 636 412 636s-116-23.3-158.4-65.6C211.3 528 188 471.8 188 412s23.3-116.1 65.6-158.4C296 211.3 352.2 188 412 188s116.1 23.2 158.4 65.6S636 352.2 636 412s-23.3 116.1-65.6 158.4z"></path></svg></i>
            <span style={{marginLeft: 8}}>搜索</span>
            </button>
        </div>);
    }
}

const mapStateToProps = (state) => {
    return {
        YxzynListData: state.findYxzynList
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        findYxzynListFun: function() {
            dispatch(findYxzynList());
        },
        
        changeAcademiesFun: function(data) {
            dispatch(changeAcademies(data));
        },
        
        changeMajorsFun: function(data) {
            dispatch(changeMajors(data));
        },
        
        initDataFun: function() {
            dispatch(initData());
        },
        
        studentfindPageFun: function(data) {
            dispatch(studentfindPage(data));
        }
    };
};

const enchce = compose(connect(mapStateToProps, mapDispatchToProps));

export default enchce(ManageSearchComponent);




